@charset "UTF-8";
@import url(https://fonts.googleapis.com/earlyaccess/cwtexfangsong.css);
@import url(direction-reveal/direction-reveal.css);
@import url(direction-reveal/site.css);
* {
  font-family: '微軟正黑體'; }

body, html {
  margin: 0px;
  padding: 0px;
  width: 100%;
  height: 100%; }

body {
  overflow-x: hidden; }

* {
  padding: 0px;
  margin: 0px;
  position: relative; }

h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
  font-weight: bold; }

a {
  text-decoration: none; }

.navbar {
  position: fixed;
  z-index: 2;
  border-bottom: solid 1px white;
  color: white;
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding: 12px 120px;
  box-sizing: border-box;
  transition-duration: 0.5s;
  box-shadow: 5px 5px 12px rgba(0, 0, 0, 0.3); }
  .navbar .navbar_logo {
    display: inline-block;
    color: white;
    transition-duration: 0.5s;
    font-size: 17.6px; }
  .navbar .navbar_list a {
    color: white;
    transition-duration: 0.5s;
    font-size: 16px; }
  .navbar .listItem {
    margin-right: 30px;
    transition-duration: 0.2s; }
    .navbar .listItem:hover {
      color: #ff6e3a; }

#mobile_navbar .button {
  width: 30px;
  height: 2px;
  background-color: #fff;
  top: 10px; }
  #mobile_navbar .button:before, #mobile_navbar .button:after {
    position: absolute;
    content: " ";
    border: solid 1px white;
    width: 30px;
    left: 0px;
    top: -10px; }
  #mobile_navbar .button:after {
    top: 10px; }

.white_bg {
  background-color: #fff; }
  .white_bg .navbar_logo {
    color: #777;
    transition-duration: 0.2s; }
    .white_bg .navbar_logo:hover {
      color: #ff6e3a; }
  .white_bg .navbar_list a {
    color: #777;
    transition-duration: 0.2s; }
    .white_bg .navbar_list a:hover {
      color: #ff6e3a; }
  .white_bg #button {
    background-color: #777; }
    .white_bg #button:before, .white_bg #button:after {
      position: absolute;
      content: " ";
      border: solid 1px #777;
      width: 30px;
      left: 0px;
      top: -10px; }
    .white_bg #button:after {
      top: 10px; }
  .white_bg .list_collect:before {
    border: solid 1px red;
    width: 50px; }

#myheader {
  background-image: url("../images/doggy.png");
  height: 500px;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: rgba(0, 0, 0, 0.5);
  background-blend-mode: overlay;
  background-attachment: fixed;
  background-position: center left; }
  #myheader #name {
    text-align: center;
    top: 30%; }
    #myheader #name h1 {
      font-size: 64px;
      color: #F8F8F8; }
    #myheader #name hr {
      width: 50px;
      border: solid 2px #ff6e3a;
      margin-left: auto;
      margin-right: auto;
      margin-top: 20px; }
  #myheader #job_title {
    text-align: center;
    top: 34%; }
    #myheader #job_title p {
      color: #F8F8F8;
      font-size: 19.2px; }
  #myheader .link_wrap {
    text-align: center;
    top: 33%;
    font-size: 40px; }
    #myheader .link_wrap #github {
      margin-right: 5px; }
    #myheader .link_wrap #medium {
      margin-left: 5px; }
  #myheader .link_wrap a {
    color: white; }
    #myheader .link_wrap a:hover {
      color: #ff6e3a; }
  #myheader #mouse_icon {
    position: absolute;
    left: 50%;
    bottom: 15%;
    border: 2px solid #fff;
    height: 40px;
    border-radius: 16px;
    width: 24px;
    margin-left: -15px;
    display: block;
    z-index: 10;
    opacity: 0.7; }
  #myheader #wheel {
    position: relative;
    border-radius: 10px;
    background: #fff;
    width: 2px;
    height: 6px;
    top: 4px;
    margin-left: auto;
    margin-right: auto;
    animation: wheel_roll 1s linear infinite; }

@keyframes wheel_roll {
  0% {
    top: 5px;
    opacity: 0; }
  30% {
    top: 10px;
    opacity: 1; }
  100% {
    top: 15px;
    opacity: 0; } }

#intro_section {
  background-color: #ff6e3a;
  display: flex;
  justify-content: center;
  padding-top: 50px;
  padding-bottom: 100px; }
  #intro_section .intro_left .intro_photo {
    background-image: url("../images/photo.jpg");
    top: 18%;
    width: 300px;
    height: 300px;
    background-position: center center;
    background-size: 80%;
    border-radius: 50%;
    margin-right: 150px;
    transition-duration: 2s; }
  #intro_section .intro_left .here {
    box-shadow: 5px 5px 20px rgba(0, 0, 0, 0.8); }
  #intro_section .intro_left .dialog {
    box-sizing: border-box;
    border: solid 1px white;
    width: 250px;
    height: 50px;
    background-color: #fff;
    margin-bottom: 15px;
    border-radius: 5px;
    top: 25%;
    box-shadow: 5px 5px 12px rgba(0, 0, 0, 0.6);
    padding: 14px 20px;
    border: solid 1px black;
    text-align: center;
    font-size: 16px; }
    #intro_section .intro_left .dialog:before {
      content: "";
      border-bottom: solid 1px black;
      border-left: solid 1px black;
      background-color: #fff;
      width: 12px;
      height: 12px;
      position: absolute;
      left: -8px;
      top: 10px;
      transform: rotate(45deg); }
  #intro_section .intro_left #dialog2 {
    margin-left: 60px; }
  #intro_section .intro_left #dialog1:after, #intro_section .intro_left #dialog2:after {
    content: "";
    position: absolute;
    height: 18px;
    border: solid 1px #ccc;
    margin-top: 2px;
    margin-left: 4px; }
  #intro_section .intro_text {
    top: 16%;
    width: 500px; }
    #intro_section .intro_text span {
      color: white; }
    #intro_section .intro_text p {
      font-size: 16px;
      line-height: 24px;
      margin-bottom: 36px; }
    #intro_section .intro_text h1 {
      margin-bottom: 30px; }

#skill_section {
  display: flex;
