      width: 30px;
      height: 50px;
      top: -25px;
      transform: rotate(15deg);
      left: 170px;
      box-shadow: 5px 5px 12px rgba(0, 0, 0, 0.3);
      background-color: white; }
  #skill_section .left_wrap2 {
    width: 400px;
