    box-shadow: 5px 5px 12px rgba(0, 0, 0, 0.3);
    background-color: #212121;
    color: white;
    padding: 30px 30px;
    transform: rotate(-3deg);
    margin-left: 150px; }
    #experience_section .exp:before {
