//COLOR SET
$color_orange: #ff6e3a;
$bg_white: #CBD4C2;
$color_white: #F8F8F8;
$sublime_pink: #F07178;
$sublime_orange: #F3C266;

//Initial Setting
$font_size: 16px;



//明體
// @import url(https://fonts.googleapis.com/earlyaccess/cwtexyen.css)

//仿宋體
@import url(https://fonts.googleapis.com/earlyaccess/cwtexfangsong.css);

