//Diraction-reveal design
// @import "node_modules/direction-reveal/styles/direction-reveal.scss"
@import "direction-reveal/direction-reveal.css";
@import "direction-reveal/site.css";

//-------------------

@mixin center {
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%); }

@mixin size($w, $h) {
	width: $w;
	height: $h; }

