body, html {
	margin: 0px;
	padding: 0px;
	width: 100%;
	height: 100%; }


body {
	overflow-x: hidden; }

* {
	padding: 0px;
	margin: 0px;
	position: relative; }

h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
	font-weight: bold; }

a {
	text-decoration: none; }


// NavBar--------------------------------------------
.navbar {
	position: fixed;
	z-index: 2;
	border-bottom: solid 1px white;
	color: white;
	display: flex;
	justify-content: space-between;
	width: 100%;
	padding: 12px 120px;
	box-sizing: border-box;
	transition-duration: 0.5s;
	box-shadow: 5px 5px 12px rgba(black, 0.3);

	.navbar_logo {
		display: inline-block;
		color: white;
		transition-duration: 0.5s;
		font-size: $font_size * 1.1; }


	.navbar_list a {
		color: white;
		transition-duration: 0.5s;
		font-size: $font_size; }

	.listItem {
		margin-right: 30px;
		transition-duration: 0.2s;
		&:hover {
			color: $color_orange; } }



	// .list_collect:before, &:after
 }	// 		background-color: #777

#mobile_navbar {
	.button {
		@include size(30px, 2px);
		background-color: #fff;
		top: 10px;
		&:before, &:after {
			position: absolute;
			content: " ";
			border: solid 1px white;
			width: 30px;
			left: 0px;
			top: -10px; }

		&:after {
			top: 10px; } } }

.white_bg {
	background-color: #fff;

	.navbar_logo {
		color: #777;
		transition-duration: 0.2s;
		&:hover {
			color: $color_orange; } }

	.navbar_list a {
		color: #777;
		transition-duration: 0.2s;
		&:hover {
			color: $color_orange; } }

	#button {
		background-color: #777;
		&:before, &:after {
			position: absolute;
			content: " ";
			border: solid 1px #777;
			width: 30px;
			left: 0px;
			top: -10px; }

		&:after {
			top: 10px; } }


	.list_collect {
		&:before {
			border: solid 1px red;
			width: 50px;
			// z-index: 2
 } } }			// display: none




//Header---------------------------------------------
#myheader {
	background-image: url("../images/doggy.png");
	height: 500px;
	background-size: cover;
	background-repeat: no-repeat;
	background-color: rgba(black, 0.5);
	background-blend-mode: overlay;
	background-attachment: fixed;
	background-position: center left;

	#name {
		text-align: center;
		top: 30%;

		h1 {
			font-size: $font_size * 4;
			color: $color_white; }
		hr {
			width: 50px;
			border: solid 2px $color_orange;
			margin-left: auto;
			margin-right: auto;
			margin-top: 20px; } }

	#job_title {
		text-align: center;
		top: 34%;

		p {
			color: $color_white;
			font-size: $font_size * 1.2; } }

	.link_wrap {
		text-align: center;
		top: 33%;
		font-size: 40px;
		#github {
			margin-right: 5px; }
		#medium {
			margin-left: 5px; } }

	.link_wrap a {
		color: white;
		&:hover {
			color: $color_orange; } }

	#mouse_icon {
		position: absolute;
		left: 50%;
		bottom: 15%;
		border: 2px solid #fff;
		height: 40px;
		border-radius: 16px;
		width: 24px;
		margin-left: -15px;
		display: block;
		z-index: 10;
		opacity: 0.7; }

	#wheel {
		position: relative;
		border-radius: 10px;
		background: #fff;
		width: 2px;
		height: 6px;
		top: 4px;
		margin-left: auto;
		margin-right: auto;
		animation: wheel_roll 1s linear infinite; }


	@keyframes wheel_roll {
		0% {
			top: 5px;
			opacity: 0; }
		30% {
			top: 10px;
			opacity: 1; }
		100% {
			top: 15px;
			opacity: 0; } } }



//Inrto Section--------------------------------
#intro_section {
	background-color: $color_orange;
	display: flex;
	justify-content: center;
	padding-top: 50px;
	padding-bottom: 100px;

	.intro_left {
		.intro_photo {
			background-image: url("../images/photo.jpg");
			top: 18%;
			@include size(300px, 300px);
			background-position: center center;
			background-size: 80%;
			border-radius: 50%;
			// box-shadow: 5px 5px 20px rgba(black, 0.6)
			margin-right: 150px;
			// left: -250px
			transition-duration: 2s; }
		.here {
			box-shadow: 5px 5px 20px rgba(black, 0.8); }

		.dialog {
			box-sizing: border-box;
			border: solid 1px white;
			@include size(250px, 50px);
			background-color: #fff;
			margin-bottom: 15px;
			border-radius: 5px;
			top: 25%;
			box-shadow: 5px 5px 12px rgba(black, 0.6);
			padding: 14px 20px;
			border: solid 1px black;
			text-align: center;
			font-size: 16px;

			&:before {
				content: "";
				border-bottom: solid 1px black;
				border-left: solid 1px black;
				background-color: #fff;
				@include size(12px,12px);
				position: absolute;
				left: -8px;
				top: 10px;
				transform: rotate(45deg); } }

		#dialog2 {
			margin-left: 60px; }

		#dialog1, #dialog2 {
			&:after {
				content: "";
				position: absolute;
				height: 18px;
				border: solid 1px #ccc;
				margin-top: 2px;
				margin-left: 4px; } } }


	.intro_text {
		top: 16%;
		width: 500px;

		span {
			color: white; }

		p {
			font-size: $font_size;
			line-height: $font_size * 1.5;
			margin-bottom: $font_size * 2.25; }

		h1 {
			margin-bottom: 30px; } } }


//Skill section------------------------------------
#skill_section {
	// height: 500px
	display: flex;
	justify-content: center;
	padding-top: 120px;
	padding-bottom: 80px;


	.left_wrap1, .left_wrap2 {
		width: 450px;
		box-shadow: 5px 5px 12px rgba(black, 0.3);
		background-color: #212121;
		color: white;
		padding: 30px 30px;
		transform: rotate(-3deg);
		margin-right: 150px;
		&:before {
			content: "";
			position: absolute;
			@include size(30px, 50px);
			top: -25px;
			transform: rotate(15deg);
			left: 170px;
			box-shadow: 5px 5px 12px rgba(black, 0.3);
			background-color: white; } }

	.left_wrap2 {
		width: 400px;
		margin-right: 0px;
		top: -40px;
		left: 280px;
		transform: rotate(5deg);
		background-color: #181818;
		&:before {
			content: "";
			position: absolute;
			transform: rotate(-15deg);
			left: 220px;
			box-shadow: 5px 5px 12px rgba(black, 0.3); } }


	span {
		color: $sublime_pink; }

	.note_title {
		font-size: 20px; }
	p {
		font-size: $font_size; }

	.right_wrap {

		h1 {
			text-align: center;
			color: $color_orange; }

		i.fas.fa-laptop-code {
			font-size: 150px;
			margin-bottom: 20px;
 } } }			// margin-top: 50px




